.App{
	text-align: center;
}

.App-logo{
	-ms-flex-align: left;
	    align-items: left;
	height: 80px;
}

.App-header{
	background-color: #333333;
	height: 80px;
	padding: 10px;
	color: #EEEEEE;
	grid-area: header;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: left;
	    align-items: left;
	-ms-flex-pack: left;
	    justify-content: left;
}

.App-title{
	font-size: 1.5em;
}

.App-intro{
	font-size: large;
}

@-webkit-keyframes App-logo-spin{
	from{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to{
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin{
	from{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to{
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}



